@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700&display=swap");

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
div,
section,
header,
footer,
h2,
h3,
p,
img {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

body {
  overflow-x: hidden;
}

/* Parent section */
.container {
  padding: 2rem;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.card {
  height: 820px;
  width: 350px;
  border-radius: 15px;
  background-color: rgba(13, 13, 13, 1);
  position: relative;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.content {
  height: 570px;
  position: absolute;
  top: 250px;
  padding: 2rem 2rem 1rem 2rem;
  border-radius: 15px;
  background-color: rgba(17, 17, 17, 1);
  z-index: 10;
}

/*Header section*/
.profile-img {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 400px;
  object-fit: cover;
}

/*Info section*/
.info {
  text-align: center;
}

.info > .name {
  font-size: 1.5rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #fff;
}

.info > .career {
  color: orange;
  margin-bottom: 0.5rem;
}

.info > .website {
  font-size: 0.9rem;
  margin-bottom: 1rem;
  color: rgba(200, 200, 200, 1);
}

.info > .social {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.social-handle {
  width: 45%;
  padding: 0.5rem;
  border-radius: 5px;
}

.social > .email {
  background-color: #fff;
  color: #000;
}

.social > .linkedin {
  background-color: steelblue;
  color: #fff;
}

.social-handle > h3 {
  font-size: 1rem;
  font-weight: 400;
}

/*About section*/
.section-to-read {
  margin-top: 2rem;
}

.section-to-read > h3 {
  color: rgba(240, 240, 240, 1);
  margin-bottom: 0.5rem;
}

.section-to-read > p {
  font-size: 0.9rem;
  line-height: 1.25;
  color: rgba(200, 200, 200, 1);
}

/*Footer section*/
.social-footer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: rgba(225, 225, 225, 1);
  margin: 2.5rem 0 1rem 0;
}

/*Font Awesome*/
.social-handle > h3 > .svg-inline--fa {
  margin-right: 5px;
}

.social-footer > .icons > svg {
  width: 30px;
  height: 30px;
}
